import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message } from 'antd';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';
import { getFeeSubmitSubjects, deleteFeeSubmitSubject } from 'services/feeSubmitSubjectType';
import './style.scss';

class FeeSubmitSubjectType extends Component {
  state = {
    reload: 0,
  };

  handleDelete = async id => {
    await deleteFeeSubmitSubject(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const columns = [
      {
        title: 'STT',
        dataIndex: 'index',
        key: 'index',
        align: 'center',
        width: '100px',
      },
      {
        title: ' Tên đối tượng',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        className: 'action-column',
        align: 'center',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: routeConstant.DANH_MUC__TAI_CHINH__DOI_TUONG_NOP_HOC_PHI__NHAP_DU_LIEU,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Hóa đơn">
        <CartHeadAction>
          <Link to={routeConstant.DANH_MUC__TAI_CHINH__DOI_TUONG_NOP_HOC_PHI__NHAP_DU_LIEU}>
            <BtnAddNew />
          </Link>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getFeeSubmitSubjects}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <FeeSubmitSubjectType />
  </Page>
);



